.details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.details-content {
  display: flex;
  align-items: center;
  width: 80vw;
  height: 80vh;
  border-radius: 10px;
}

.details-image {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  height: auto;
}

.details-image img {
  max-width: 100%;
  max-height: 100%;
}

.details-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  margin: 10px;
}

.details-section {
  width: 30vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 20px;
}

.details-item {
  display: flex;
  padding: 20px 25px;
  margin: 1px 13px;
  flex-direction: column;
  align-items: center;
  flex: 1 0 0;
  border-radius: 32px;
  border: 1px solid rgba(25, 24, 37, 0.10);
  background: #FFF;
}

h4 {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  color: #F85E9F;
  font-family: Nunito Sans;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 4.6px;
  text-transform: uppercase;
}

h2 {
  align-items: flex-start;
  display: flex;
  align-self: stretch;
  color: #191825;
  font-family: Nunito Sans;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  text-align: left;
}

p {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  color: rgba(25, 24, 37, 0.50);
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

h5 {
  color: #FF5722;
  font-family: Nunito Sans;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  text-align: center;
}

.details-item p {
  color: #191825;
  font-family: Inter;
  font-size: 17px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  text-align: center;
}

.rectangle {
  position: absolute;
  width: 700px;
  height: 500px;
  top: 234.5%;
  border-radius: 0px 500px 500px 0px;
  background: #FACD49;
  z-index: -1;
}

.ellipse-1 {
  position: absolute;
  top: 220%;
  left: 38%;
  max-width: 100px;
}

.ellipse-2 {
  position: absolute;
  top: 231%;
  left: 14%;
  max-width: 80px;
}

.ellipse-3 {
  position: absolute;
  top: 247%;
  left: 46%;
  max-width: 60px;
}

.ellipse-4 {
  position: absolute;
  top: 258%;
  left: 36%;
  max-width: 50px;
}

.ellipse-5 {
  position: absolute;
  top: 275%;
  left: 41%;
  max-width: 70px;
}

.star {
  display: inline-flex;
  position: absolute;
  top: 260%;
  left: 78%;
  padding: 20px;
  align-items: center;
  border-radius: 100px;
  background: #FACD49;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.03),
    0px 15px 34px 0px rgba(0, 0, 0, 0.03),
    0px 62px 62px 0px rgba(0, 0, 0, 0.02),
    0px 139px 83px 0px rgba(0, 0, 0, 0.01),
    0px 246px 99px 0px rgba(0, 0, 0, 0.00),
    0px 385px 108px 0px rgba(0, 0, 0, 0.00);
}

/* Media Queries for Mobile Responsiveness */
@media only screen and (max-width: 768px) {
  .details-container {
    height: auto; /* Adjust height for smaller screens */
  }

  .details-content {
    flex-direction: column; /* Change to column layout on smaller screens */
    width: 90vw; /* Adjust width for smaller screens */
    height: auto; /* Adjust height for smaller screens */
  }

  .details-image {
    max-width: 100%; /* Adjust max width for smaller screens */
  }

  .details-section {
    width: 100%; /* Adjust width for smaller screens */
    gap: 10px; /* Adjust gap for smaller screens */
  }

  .details-item {
    padding: 10px 15px; /* Adjust padding for smaller screens */
    margin: 5px 5px; /* Adjust margin for smaller screens */
  }

  h2 {
    font-size: 32px; /* Adjust font size for smaller screens */
  }

  p {
    font-size: 14px; /* Adjust font size for smaller screens */
    line-height: 140%; /* Adjust line height for smaller screens */
  }

  .rectangle {
    width: 100%; /* Adjust width for smaller screens */
    height: 400px; /* Adjust height for smaller screens */
    top: 200%; /* Adjust position for smaller screens */
  }

  .ellipse-1,
  .ellipse-2,
  .ellipse-3,
  .ellipse-4,
  .ellipse-5,
  .star {
    display: none; /* Hide elements on smaller screens */
  }
}
