.home-container {
  justify-content: center;
  display: flex;
  align-items: center;
  margin-top: 3%;
}

.images-container {
  left: 0%;
  display: flex;
  align-items: center;
  gap: 31px;
  padding: 10px;
  position: relative; /* Add position relative */
}

.background-image {
  position: absolute;
  top: 20%;
  left: 45%;
  width: 35%;
  height: auto;
  z-index: -1;
}

.column {
  display: flex;
  flex-direction: column;
  gap: 31px;
}

.third-image {
  margin-left: auto;
}

.rounded-image {
  border-radius: 50%;
  margin-bottom: 10px;
  width: 11vw;
  height: auto;
  border-radius: 32px;
  background: white;
}

.text-section {
  display: flex;
  width: 380px;
  flex-direction: column;
  align-items: flex-start;
  gap: 43px;
}

.explore-button {
  display: flex;
  padding: 1vw 2.5vw;
  align-items: center;
  gap: 16px;
  border-radius: 100px;
  background: #fff;
  color: #f85e9f;
  font-size: 14px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.bag-icon {
  width: 24px;
  height: 24px;
}

h1 {
  color: #000;
  font-size: 4vw;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
  text-align: left;
}

.top-destination {
  color: #f85e9f;
}

p {
  color: rgba(25, 24, 37, 0.50);
  font-size: 1.2vw;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
}

.button-group {
  display: flex;
  align-items: flex-start;
  gap: 16px;
}

button:hover {
  opacity: 0.85;
}

.round-button1 {
  display: flex;
  padding: 1.2vw 2vw;
  align-items: flex-start;
  gap: 8px;
  border-radius: 100px;
  background: #5d50c6;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10),
    0px 5px 11px 0px rgba(0, 0, 0, 0.10),
    0px 20px 20px 0px rgba(0, 0, 0, 0.09),
    0px 45px 27px 0px rgba(0, 0, 0, 0.05),
    0px 81px 32px 0px rgba(0, 0, 0, 0.01),
    0px 126px 35px 0px rgba(0, 0, 0, 0.00);
  color: #eee;
  font-size: 14px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 2vh;
}

.round-button2 {
  display: flex;
  padding: 1vw 2vw;
  align-items: center;
  gap: 8px;
  border-radius: 100px;
  border: 1px solid #eee;
  background: #fff;
  color: #222831;
  font-size: 14px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 2vh;
}

.play-icon {
  width: 24px;
  height: 24px;
}

.round-button:last-child {
  margin-right: 0;
}

.bottom-div {
  display: flex;
  width: 100%;
  height: auto;
  padding: 10px;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 50px;
}

.bottom-div img {
  max-width: 20vw;
  height: auto;
  margin-left: 20px;
  overflow: hidden;
}

.footer-img {
  padding-left: 150px;
}

.footer-img2 {
  padding-right: 150px;
}

.objects-img {
  width: 4vw;
  height: auto;
  position: absolute;
  bottom: 100px;
  left: 50px;
  top: 550px;
}

.background-image-line {
  position: absolute;
  top: 20%;
  width: 10vw;
  left: 45%;
  z-index: -1;
}

.background-image-ap {
  position: absolute;
  top: 18%;
  width: 1vw;
  left: 49.5%;
  z-index: -1;
}

.background-image-line2 {
  position: absolute;
  top: 20%;
  width: 6vw;
  left: 70.5%;
  z-index: -1;
}

.background-image-ap2 {
  position: absolute;
  top: 18.7%;
  width: 1vw;
  left: 69.5%;
}

/* Media Queries for Mobile Responsiveness */
@media only screen and (max-width: 768px) {
  .home-container {
    flex-direction: column;
  }

  .images-container {
    flex-direction: row;
    align-items: center;
  }

  .background-image {
    top: 10%;
    left: 30%;
    width: 40%;
  }

  .rounded-image {
    width: 30vw;
  }

  .text-section {
    width: 90%;
    align-items: center;
    text-align: center;
  }

  h1 {
    font-size: 6vw;
  }

  p {
    font-size: 3vw;
  }

  .button-group {
    flex-direction: column;
    align-items: center;
  }

  .round-button1,
  .round-button2 {
    padding: 4vw 8vw;
    font-size: 4vw;
    line-height: 8vw;
  }

  .bottom-div {
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
  }

  .bottom-div img {
    display: none;
  }
}