/* Common styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  padding: 10px;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 2vw;
}

.app-name {
  color: var(--black, #191825);
  font-size: 24px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 900;
}

.nav-links {
  display: flex;
  align-items: flex-start;
  gap: 75px;
}

.nav-link {
  color: #222831;
  font-size: 14px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.user-actions {
  display: flex;
  align-items: center;
}

.signup-btn {
  font-size: 14px;
  color: #eee;
  font-family: Nunito Sans;
  font-style: normal;
  display: flex;
  padding: 8px 26px;
  align-items: flex-start;
  border-radius: 100px;
  background: #5d50c6;
}

.corner-div {
  width: 500px;
  height: 500px;
  border-radius: 500px;
  opacity: 0.5;
  background: #ff5722;
  filter: blur(250px);
  position: absolute;
  bottom: 100px;
  left: -400px;
  right: 0px;
  top: -200px;
}

/* Media queries for mobile responsiveness */
@media (max-width: 768px) {
  .navbar {
    justify-content: center;
    padding: 10px 20px;
  }

  .nav-links {
    display: none;
  }

  .user-actions {
    margin-left: auto;
  }

  .signup-btn {
    font-size: 12px;
    padding: 6px 16px;
  }
}

@media (max-width: 480px) {
  .app-name {
    font-size: 20px;
  }

  .signup-btn {
    font-size: 10px;
    padding: 4px 12px;
  }
}
