.services-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 100px;
  overflow: hidden;
}

.services-header {
  width: 900px;
  padding: 20px;
}

.services-header h3 {
  display: flex;
  align-items: flex-start;
  color: #f85e9f;
  font-family: Nunito Sans;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 4.6px;
  text-transform: uppercase;
}

.services-header p {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  color: #191825;
  font-family: Nunito Sans;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.services-list {
  display: flex;
  align-items: flex-start;
  gap: 18px;
  overflow-x: auto;
  padding-bottom: 10px;
}

.services-list::-webkit-scrollbar {
  display: none;
}

.service-item {
  margin: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  min-width: 270px;
  max-width: 350px;
  min-height: 350px;
  padding: 20px;
  border-radius: 32px;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10),
    0px 12px 20px 0px rgba(0, 0, 0, 0.10);
}

.service-item img {
  width: 64px;
  height: 64px;
}

.service-item h4 {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  color: #191825;
  text-align: center;
  font-family: Nunito Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.service-item p {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  color: rgba(25, 24, 37, 0.50);
  text-align: center;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

/* Media Queries for Mobile Responsiveness */
@media only screen and (max-width: 768px) {
  .container{
    height: auto;
  }

  .services-header {
    width: 100%; 
    padding: 10px; 
  }

  .services-header h3 {
    font-size: 15px; 
    letter-spacing: 2px; 
  }

  .services-header p {
    font-size: 18px; 
  }

  .services-list {
    overflow-x: scroll; 
    padding-bottom: 0; 
  }

  .service-item {
    margin: 15px 15px; 
    min-width: 150px; 
    min-height: 200px; 
  }

  .service-item h4 {
    font-size: 16px; 
  }

  .service-item p {
    font-size: 12px; 
    line-height: 140%; 
  }
}
