.subscribe-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.subscribe-content {
  display: flex;
  width: 1110px;
  padding: 128px 64px;
  flex-direction: column;
  align-items: center;
  gap: 64px;
  border-radius: 32px;
  background: rgba(250, 205, 73, 0.08);
}

.subscribe-sec {
  display: flex;
  align-items: flex-start;
  gap: 64px;
  align-self: stretch;
}

.sub-header3 {
  display: flex;
  width: 982px;
  flex-direction: column;
  color: #F85E9F;
  text-align: center;
  font-family: Nunito Sans;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 4.6px;
  text-transform: uppercase;
}

.sub-header1 {
  display: flex;
  width: 982px;
  flex-direction: column;
  color: #191825;
  text-align: center;
  font-family: Nunito Sans;
  font-size: 55px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.input-container {
  display: flex;
  padding: 20px;
  align-items: center;
  gap: 10px;
  flex: 1 0 0;
  border-radius: 32px;
  background: #FFF;
}

input {
  width: 600px;
  height: 40px;
  padding: 4px;
  border-radius: 5px;
  border: 1px solid #ffffff;
  font-size: 16px;
}

input::placeholder {
  color: #999;
}

.subscribe-btn {
  padding: 10px 50px;
  align-items: center;
  gap: 16px;
  align-self: stretch;
  border-radius: 32px;
  background: #5D50C6;
  color: white;
  font-size: 18px;
}

.subscribe-btn:hover {
  background-color: #ed448f;
}

.graphic-element{
  z-index: 1;
  position: absolute;
  top: 319%;
  left: 10.5%;
}

/* Media Queries for Mobile Responsiveness */
@media only screen and (max-width: 768px) {
  .subscribe-container{
    margin: 20px;
    height: auto;
    position: absolute;
    overflow: hidden;
    top: 350%;
  }

  .subscribe-content {
    width: 90vw; 
    padding: 64px; 
  }

  .sub-header1 {
    font-size: 20px; 
  }

  .subscribe-sec {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .input-container {
    display: flex;
    padding: 12px;
    align-items: center;
    background: #FFF;
  }

  input {
    width: 100%; 
  }

  .subscribe-btn {
    padding: 10px 30px; 
    font-size: 12px; 
    width: 200px;
    margin-left: 60px;
  }

  .graphic-element {
    display: none; 
  }
    
  .sub-header3 {
    width: 400px;
    align-items: center;
  }

  .sub-header1 {
    width: 400px;
    
  }
}
